/* Main Nav Logo */
nav {
  background-color: white; }
  nav ul a, nav .brand-logo {
    color: #444; }

.brand-logo img {
  height: 40px;
  padding: 0 6px;
  vertical-align: middle; }

nav .brand-logo {
  padding: 0 1rem; }

.search-wrapper {
  width: 400px; }
  @media screen and (max-width: 1210px) {
    .search-wrapper {
      width: 200px; } }
  .side-nav .search-wrapper {
    padding: 0 15px;
    width: 100%; }

.collapsible .collapsible-header {
  font-weight: 500; }

.collapsible .main {
  border-bottom: solid 2px #bbb6b6;
  font-weight: 300;
  font-size: 1.4rem; }

.collapsible ul {
  color: #999;
  font-size: 0.9rem; }
